import { transition, trigger, style, animate, state } from '@angular/animations';
import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { Note } from 'src/app/shared/note.module';
import { NotesService } from 'src/app/shared/notes.service';

@Component({
  selector: 'app-note-details',
  templateUrl: './note-details.component.html',
  styleUrls: ['./note-details.component.scss'],
  animations: [
    trigger('pageChange',[
      state('void',style({
        transform: 'translateX(100%)',
        height: '*'
      })),
      transition('void => *', [
        animate('350ms ease-out')
      ])
    ])
  ]
})
export class NoteDetailsComponent implements OnInit {

  note: Note;
  noteId: number;
  new: boolean;

  constructor(private noteService: NotesService, private router: Router, private route: ActivatedRoute) { }

  ngOnInit(): void {
    this.route.params.subscribe((params: Params) => {
      this.note = new Note();
      if (params.id){
        this.note = this.noteService.get(params.id);
        this.noteId = params.id;
        this.new = false;
      } else{
        this.new = true;
      }
    })
  }

  onSubmit(form: NgForm){
    if (this.new){
      this.noteService.add(form.value);
    } else{
      this.noteService.update(this.noteId, form.value.title, form.value.note);
    }
    this.router.navigateByUrl("/");
  }

  titleValidation(text:string){
    const titleInput = document.getElementById('title');
    if(text.length > 0){
      titleInput.style.background = "#eeeeee";
    }
    else{
      titleInput.style.background = "#ffd4db";
    }
  }
}
